import React, {PureComponent} from 'react';
import Row from 'r-cmui/components/Row';
import Col from 'r-cmui/components/Col';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';
import Demo5 from './demo5';
import Demo6 from './demo6';
import Demo7 from './demo7';

class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Button </h1>
                <blockquote className='page-tip'>
                    
                </blockquote>

                <h1 className='page-h1'></h1>

                <Row>
                    <Col grid={0.5} className='code-col'>
                        <Label className='code-box'>
                            <Demo1></Demo1>
                        </Label>
                        <Label className='code-box'>
                            <Demo3></Demo3>
                        </Label>
                        <Label className='code-box'>
                            <Demo5></Demo5>
                        </Label>
                        <Label className='code-box'>
                            <Demo7 />
                        </Label>
                    </Col>
                    <Col grid={0.5} className='code-col'>
                        <Label className='code-box'>
                            <Demo2></Demo2>
                        </Label>
                        <Label className='code-box'>
                            <Demo4></Demo4>
                        </Label>
                        <Label className='code-box'>
                            <Demo6 />
                        </Label>
                    </Col>
                </Row>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Button</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'theme', desc: 'default primary success warning danger', type: 'string', default: 'default'},
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'disabled', desc: '', type: 'boolean', default: 'false'},
                    {param: 'raised', desc: '', type: 'bool', default: 'false'},
                    {param: 'flat', desc: '', type: 'bool', default: 'false'},
                    {param: 'href', desc: '', type: 'string', default: ''},
                    {param: 'icon', desc: '', type: 'string', default: ''},
                    {param: 'circle', desc: '', type: 'boolean', default: 'false'},
                    {param: 'iconAlign', desc: ' left right', type: 'string', default: 'left'},
                    {param: 'size', desc: ' large small', type: 'string', default: ''},
                    {param: 'target', desc: 'atarget', type: 'string', default: ''},
                    {param: 'onClick', desc: '', type: 'function', default: ''},
                    {param: 'animation', desc: '', type: 'bool', default: 'true'},
                    {param: 'loadding', desc: 'loadding', type: 'bool', default: 'false'}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>disable()</i>
                        <span></span>
                    </li>
                    <li>
                        <i>enable()</i>
                        <span></span>
                    </li>
                    <li>
                        <i>setActive(active)</i>
                        <span> ButtonGroup</span>
                        <ul>
                            <li>
                                active {'Boolean'} true false 
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        );
    }
}
export default Page;



